@imgSize: 150px;

.container {
  width: 1200px;
  margin: 20px auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  text-align: center;
  gap: 35px 0;
  img {
    width: @imgSize;
    height: 200px;
  }
}

.title {
  font-size: 14px;
  margin: 10px 0 5px 0;
}

.rate {
  color: #e4a64a;
  font-size: 12px;
}
