.pager {
  margin: 20px;
  display: flex;
  justify-content: center;
  > a {
    color: #4cb1f5;
    padding: 0 5px;
    margin: 5px;
    cursor: pointer;
    /* 用户选择 */
    user-select: none;
    &.active {
      border: none;
      color: #f40;
      cursor: auto;//用户无法点击
    }
    &.disabled {
      color: #ccc;
      cursor: not-allowed;
    }
  }
}
